/* Base CSS */

@font-face {
  font-family: RusticLoveTattoo;
  src: url(../fonts/RusticLoveTattoo.otf);
}

.alignleft {
  float: left;
  margin-right: 15px;
}

.alignright {
  float: right;
  margin-left: 15px;
}

.aligncenter {
  display: block;
  margin: 0 auto 15px;
}

a:focus {
  outline: 0 solid;
}

a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h3 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h4 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h5 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h6 {
  margin: 0 0 15px;
  color: #1B1B21;
}

body {
  color: #545051;
  font-family: "Open Sans", sans-serif;
}

.phone-embed {
  position: absolute;
  z-index: 1;
  width: 200px;
  height: 327px;
  background: url("../img/phone.png") no-repeat;
  background-size: 95%;
  margin-left: auto;
  margin-right: auto;
  transform: rotate(-5deg);
}

@media (min-width: 320px) {
  .phone-embed {
    width: 230px;
    margin-top: -280px;
  }
}

@media (min-width: 992px) {
  .phone-embed {
    position: absolute;
    top: 450px;
    left: 20px;
    z-index: 999;
    margin-top: 0;
  }
}

.phone-embed iframe {
  position: absolute;
  top: 9px;
  left: 40px;
  width: 140px;
  border-radius: 15px;
  height: 302px;
}

.phone-embed .gabe {
  position: absolute;
  top: 150px;
  right: -150px;
  font-size: 10pt;
  color: #666;
  z-index: 9999;
  width: 180px;
  transform: rotate(-3deg);
  font-family: "BenchNine";
}

.hero {
