.inspired-images .grid-item a {
  display: block;
  width: 50%;
  margin-bottom: 4px;
  transition: transform 300ms;
  transform-origin: 50% bottom;
}

.inspired-images .grid-item a:active {
  transform: perspective(500px) rotateX(0.13rad);
}

.inspired-images .grid-item a:first-child {
  padding-right: 2px;
}

.inspired-images .grid-item a:last-child {
  padding-left: 2px;
}

@keyframes hasbro-toys-animation {
  from {
    transform: translateY(300px);
  }

  to {
    transform: translateY(0);
  }
}

